<template>
<div id="page-wrap">

    <ProductsGrid :products="products"/>

</div>
</template>



<script type="text/javascript">
import axios from 'axios'; 
import ProductsGrid from'../components/ProductsGrid.vue'
  export default { /*Isso permite outros componentes importar e mostrar o componente ProductsPage.vue (esse aí) */
    name: 'ProductPage', /*Nome usado pra referenciar dentro de outros componentes*/
    components: {
      ProductsGrid
    },
data() {
return {
products: [],
};
},
async created() {
	const result = await axios.get('/api/products');
	const products = result.data;
	this.products =  products;

} 
  };
  
</script>