<template>
  <div id="page-wrap">
    <Cart :products= "cartItems"
    v-on:remove-from-cart="removeFromCart($event)"

    />
     <h3 id="total-price">Preço total: ${{ totalPrice }}</h3>
    <button id="checkout-button">Pagar Agora</button>

  </div>
	
</template>

<script type="text/javascript">
  import axios from 'axios';
  import Cart from '../components/Cart.vue';

	export default { /*Isso permite outros componentes importar e mostrar o componente CartPage.vue (esse aí) */
		name: 'CartPage',
    components: {Cart
    }, /*Nome usado pra referenciar dentro de outros componentes*/
    data() {
      return {
        cartItems: [],
      }
    },
    computed: {
      totalPrice() {
        return this.cartItems.reduce(
          (sum, item)=>sum + Number(item.price), 0)
      }
    },
    methods: {
      async removeFromCart(productId){
        const result = await axios.delete(`/api/users/12345/cart/${productId}`);
        this.cartItems = result.data;
      }
    },
    async created() {
      const result = await axios.get('/api/users/12345/cart');
      const cartItems = result.data;
      this.cartItems = cartItems;
    },

	};
	

</script>

