import Vue from 'vue'
import VueRouter from 'vue-router'
import CartPage from '../views/CartPage.vue'
import ProductDetailPage from '../views/ProductDetailPage.vue'
import ProductsPage from '../views/ProductsPage.vue'
import NotFoundPage from '../views/NotFoundPage.vue'

Vue.use(VueRouter)

const routes = [

  {
    path: '/products/', /*é o URL*/
    name: 'Products', /*é bom dar os nomes aos routes*/ 
    component: ProductsPage
  }, {

    path: '/products/:id', /*é o URL. id é um parametro, que permite pegar o valor da seção e usar isso pra escolher o que a gente quer mostrar na página.   */
    name: 'ProductDetail', /*é bom dar os nomes aos routes*/ 
    component: ProductDetailPage

  }, {

    path: '/cart/', /*é o URL. id é um parametro, que permite pegar o valor da seção e usar isso pra escolher o que a gente quer mostrar na página.   */
    name: 'Cart', /*é bom dar os nomes aos routes*/ 
    component: CartPage

  }, {
    path: '/',
    redirect: '/products/'

  }, {
    path: '*',
    name: 'NotFoundPage',
    component: NotFoundPage,

  }
  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
